import 'semantic-ui-css/semantic.min.css'
import './App.css';
import { Button, Card, Label, Icon, Image } from 'semantic-ui-react'
import React, {Component, useState, useEffect} from 'react'
import ReactPlayer from 'react-player'


const MediaMessages = (props) =>{
    let media = null
    let path = props.props.path
    if(path.includes('videos')){
        console.log(props.props.path)
        path = path.replace('videos', 'video')
        media = (<ReactPlayer url={path} width="270px" controls={true}/>)
    } else {
        media = (<Image floated='left' size='medium' src={props.props.path}/>)
    }

    let reaction_div = []
    for (var key in props.props.reaction_dict) {
        // check if the property/key is defined in the object itself, not in parent
        reaction_div.push(
            <Label as='a' image>
                <span role="img" aria-label="sheep">{key}</span>
                {props.props.reaction_dict[key]}
            </Label>
        )
    }

    return (
        <div>
        <Card>
            <Card.Content>
            <Image
                floated='left'
                size='mini'src={'pictures/profiles/'+ props.props.person + '.jpg'}
            />
            <Card.Header>{props.props.person}</Card.Header>
            <Card.Meta>12/11/2020</Card.Meta>
            <Card.Description>
                {media}
                "<strong className="message-text">{props.props.message}</strong>""
            </Card.Description>
            </Card.Content>
            <Card.Content extra>
            {reaction_div}
            </Card.Content>
        </Card>
        </div>
    )
}


export default MediaMessages;
