import 'semantic-ui-css/semantic.min.css'
import { Button, Card, Label, Icon, Image } from 'semantic-ui-react'
import React, {Component, useState, useEffect} from 'react'



const Messages = (props) =>{

    let reaction_div = []
    for (var key in props.props.reaction_dict) {
        // check if the property/key is defined in the object itself, not in parent
        reaction_div.push(
            <Label as='a' image>
                <span role="img" aria-label="sheep">{key}</span>
                {props.props.reaction_dict[key]}
            </Label>
        )
    }


    return (
        <Card>
            <Card.Content>
            <Image
                floated='left'
                size='mini'
                src={'pictures/profiles/'+ props.props.person + '.jpg'}
                 
            />
            <Card.Header>{props.props.person}</Card.Header>
            <Card.Meta>12/11/2020</Card.Meta>
            <Card.Description>
                <Image
                floated='left'
                size='medium'
                />
                {props.props.message}
            </Card.Description>
            </Card.Content>
            <Card.Content extra>
                {reaction_div}
            </Card.Content>
        </Card>
    )
}


export default Messages;
