import './App.css';
import 'semantic-ui-css/semantic.min.css'
import { Statistic } from 'semantic-ui-react'
import LoadingBar from 'react-top-loading-bar'
import { Image, Item } from 'semantic-ui-react'
import { Button, Card, Label, Icon } from 'semantic-ui-react'

// import { Chart } from 'react-charts'
import Chart from "react-apexcharts";

// import {Parallax, ParallaxLayer} from 'react-spring/renderprops-addons'
import { Parallax } from 'react-parallax';

import React, {Component, useState, useEffect} from 'react'
import headerimage from './images/new-header.png'; // Tell webpack this JS file uses this image
import otherimage from './images/IMG-1878.JPG'; // Tell webpack this JS file uses this image
import adsimage from './images/IMG-1200.PNG'; // Tell webpack this JS file uses this image

import oneimage from './images/1.jpg'; // Tell webpack this JS file uses this image
import twoimage from './images/2.jpg'; // Tell webpack this JS file uses this image
import threeimage from './images/3.jpg'; // Tell webpack this JS file uses this image
import fourimage from './images/4.jpg'; // Tell webpack this JS file uses this image
import fiveimage from './images/5.jpg'; // Tell webpack this JS file uses this image
import siximage from './images/6.jpg'; // Tell webpack this JS file uses this image
import Messages from './Messages'
import MediaMessages from './MediaMessages'





const App = () =>{

  const [progress, setProgress] = useState(0)
  const [data, setData] = useState([]);
  const [topMessages, setTopMessages] = useState([]);
  const [topMediaMessages, setTopMediaMessages] = useState([]);


  const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
  }

  const getData = () => {
      fetch('data/data_file_2020.json', {
        headers : { 
          'Content-Type': 'application/json',
          'Accept': 'application/json'
          }
        }
      ).then(function(response){
        return response.json();
      }).then(function(myJson) {
        console.log(myJson);
        setData(myJson)
      });
  }

  useEffect(() =>{
    // console.log
    let topMessagesTemp = []
    let topMediaMessagesTemp = []
    if(data.length != 0){
      data.top_messages.forEach(element => {
        topMessagesTemp.push(<Messages props={element}></Messages>)
      });
      setTopMessages(topMessagesTemp)

      data.top_media.forEach(element => {
        topMediaMessagesTemp.push(<MediaMessages props={element}></MediaMessages>)
      });
      setTopMediaMessages(topMediaMessagesTemp)
      console.log(topMessagesTemp)
      console.log(topMediaMessagesTemp)
      
    }
  }, [data])

  

  const mimicLoading = async () => {
      let nums = [10,20,40,60,100]
      for (const num of nums) {
          setProgress(num)
          await sleep(200)
        }
  }      

  // Runs on page load
  useEffect(() =>{
      mimicLoading()    
      getData()
  }, [])

  const inlineStyle = {
    width: '100%',
    background: '#fff',
    left: '50%',
    top: '50%',
    position: 'absolute',
    padding: '20px',
    transform: 'translate(-50%, -50%)',
  }



  return (
    <div>
      <LoadingBar color="#C70039" progress={progress} onLoaderFinished={() => setProgress(0)} />
      <img src={headerimage} alt="header" className="header-image"/>
      {data['year'] == 2020 ? (
      <div className="content-container">
        <Parallax bgImage={ otherimage }  blur={{ min: -1, max: 3 }}>
          <div style={{ height: 500 }}>
            <div style={inlineStyle}>
            <div className="stat-group">
              <h1> Overall Statistics</h1>
                <Statistic.Group size="small" horizontal={true}  className="stats-group">
                  <Statistic>
                    <Statistic.Value>{data.overall_stats.total_messages}</Statistic.Value>
                    <Statistic.Label>Total Messages</Statistic.Label>
                  </Statistic>
                  <Statistic>
                    <Statistic.Value>{data.overall_stats.total_reactions}</Statistic.Value>
                    <Statistic.Label>Total Reacts</Statistic.Label>
                  </Statistic>
                  
                  <Statistic >  
                    <Statistic.Value>{data.overall_stats.total_photos}</Statistic.Value>
                    <Statistic.Label>Total Pictures</Statistic.Label>
                  </Statistic>
                </Statistic.Group>
              </div>
            </div>
          </div>
        </Parallax>
        <h1 className="divider">| | |</h1>
        <Parallax bgImage={ twoimage } >
          <div style={{ height: 500 }}>
            <div style={inlineStyle}>
              <div className="stat-group">
                <h1> Year Statistics</h1>
                  <Statistic.Group size="small" horizontal={true}  className="stats-group">
                    <Statistic>
                      <Statistic.Value>{data.year_stats.total_messages}</Statistic.Value>
                      <Statistic.Label>Total Messages</Statistic.Label>
                    </Statistic>
                    <Statistic>
                      <Statistic.Value>{data.year_stats.total_reactions}</Statistic.Value>
                      <Statistic.Label>Total Reacts</Statistic.Label>
                    </Statistic>
                    <Statistic >  
                      <Statistic.Value>{data.year_stats.total_photos}</Statistic.Value>
                      <Statistic.Label>Total Pictures</Statistic.Label>
                    </Statistic>
                  </Statistic.Group>
                </div>
              </div>
          </div>
        </Parallax>
        <h1 className="divider">| | |</h1>
        <Parallax bgImage={ threeimage }  blur={{ min: -1, max: 5 }}>
          <div style={{ height: 500 }}>
            <div style={inlineStyle}>
              <div className="stat-group">
                <h1> Year Records</h1>
                  <Statistic.Group size="small" horizontal={true}  className="stats-group">
                    <Statistic>
                      <Statistic.Value>20-01-2020 ({data.year_record.most_messages.day.amount})</Statistic.Value>
                      <Statistic.Label>Most Messages in one day</Statistic.Label>
                    </Statistic>
                    <Statistic>
                      <Statistic.Value>March ({data.year_record.most_messages.month.amount})</Statistic.Value>
                      <Statistic.Label>Most Messages in one month</Statistic.Label>
                    </Statistic>
                    <Statistic >
                      <Statistic.Value>{data.year_record.most_liverpool_mentions.person} ({data.year_record.most_liverpool_mentions.amount})</Statistic.Value>
                      <Statistic.Label>Most Liverpool Mentions</Statistic.Label>
                    </Statistic>
                  </Statistic.Group>
                </div>
              </div>
          </div>
        </Parallax>
        <h1 className="divider">| | |</h1>
        {/* <Parallax 
          bgImage={ adsimage }
          strength={200}>
          <div style={{ height: 9000 }}>
            <div style={inlineStyle}>
              <h1>Top Messages</h1>
              <div>
                {topMessages}
              </div>
            </div>
          </div>
        </Parallax> */}
        <div class="messages">
          <h1>Top Messages</h1>
          <div className="message-div">
            {topMessages}
          </div>
        </div>
        <h1 className="divider">| | |</h1>
        <div>

        <div class="messages">
          <h1>Top Pictues</h1>
            <div className="message-div">
              {topMediaMessages}
            </div>
          </div>
        </div>
        {/* <Chart data={data} axes={axes} /> */}
        <div style={{ height: '100vh' }}></div>
      </div> ) : null}
    </div> 
  );
}


export default App;

